<template>
    <div>
        <div v-for="i in content">
        <dv-border-box-7 id="container" :color="['#45C2E0','#C1EBDD']">
            <div id="block1">
                <div id="index"><p>{{ i.camera_id }}号摄像头</p></div>
                <div id="type"><p>{{ i.warn_type }}</p></div>
            </div>
            <div id="block2">
                <div id="time1"><p>警报时间:</p></div>
                <div id="time2"><p>{{ i.warn_time }}</p></div>
            </div>
            <div id="description"><p>{{ i.warn_description }}</p></div>
            <div style="text-align: right">
                <h7 v-if="i.id==showModel">已删除&ensp;</h7>
                <button type="button" class="btn btn-primary btn-sm" @click="show(i.id)"><p>处理</p></button>
            </div>
        </dv-border-box-7>
        </div>
    </div>
</template>

<script>
import popup from "./Popup.vue"
export default{
    components:{
			popup,
    },
    data(){
			return{
				content:null,
                showModel:0,
			}
		},
    created(){
        this.getdata();
    },
	mounted(){
        this.refreshdata=setInterval(() => {
		    this.getdata();
        }, 2000);
	},
    destroyed(){
        clearInterval(this.refreshdata);
        this.refreshdata=null;
    },
	methods:{
		getdata(){
			this.axios.get('http://localhost:8000/api/warn/')
			.then(res=>{
				this.content=res.data.result
			})
            this.showModel=0
		},
        show(id){
				this.showModel=id
                this.axios.get('http://localhost:8000/api/warn/',{
					params:{
						id:id,
					}
				})
				.then(res=>{
					this.content=res.data.result
				})
		},
    }
}
</script>

<style scoped>  
#container{
    width: 100%;
    height: 50%;
    padding: 3%;
    margin-bottom: 5%;
}
#block1{
    display: flex;
    padding: 1%;
}
#index{
    flex: 1;
    text-align: center;
    background-color: rgba(255,255,255,.2);
    border-radius: .25em;
    border: 0px solid #dc3545;
    border-left-width: .25em;
}
#type{
    flex: 2;
    text-align: center;
}
#block2{
    display: flex;
    padding: 1%;
}
#time1{
    flex: 1;
    text-align: center;
    background-color: rgba(255,255,255,.2);
    border-radius: .25em;
    border: 0px solid #dc3545;
    border-left-width: .25em;
}
#time2{
    flex: 2;
    text-align: center;
}
#description{
    padding: 1%;
}
p{
    margin-bottom: 0;
    color: white;
    font-size: 13px;
    font-weight: lighter;
}
h7{
    margin-bottom: 0;
    color: #dc3545;
    font-weight: bolder;
}
</style>