<template>
    <e-charts class="chart" :option="option"></e-charts>
</template>

<script>
export default{
    data(){
        return{
            option:{
                color:['#45C2E0', '#C1EBDD', '#FFC851'],
                tooltip: {
                    trigger: 'item',
                },
                legend: {
                    textStyle: {
                        show: true,
                        color: '#ffffff',
                        fontSize: 10
                    }
                },
                series: [
                    {
                    label: {
                        color: '#ffffff',
                        fontSize: 10
                    },
                    type: 'pie',
                    radius: '40%',
                    data: [
                        { value: null, name: '滞留量' },
                        { value: null, name: '流入量' },
                        { value: null, name: '流出量' },
                    ],
                    emphasis: {
                        itemStyle: {
                        shadowBlur: 10,
                        shadowOffsetX: 0,
                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                        }
                    }
                    }
                ]
            }
        }
    },
    created(){
        this.getdata();
    },
	mounted(){
        this.refreshdata=setInterval(() => {
		    this.getdata();
        }, 2000);
	},
    destroyed(){

        clearInterval(this.refreshdata);
        this.refreshdata=null;
    },
	methods:{
		getdata(){
            this.axios.get('http://localhost:8000/graph_vis_vue')
            .then(res=>{
					this.content=res.data
				})
            this.option.series[0].data[0].value=this.content.pie.keep
            this.option.series[0].data[1].value=this.content.pie.in
            this.option.series[0].data[2].value=this.content.pie.out
		},
	}
}
</script>