<template>
    <e-charts class="chart" :option="option"></e-charts>
</template>

<script>
export default{
    data(){
        return{
            option:{
                color:['#45C2E0', '#C1EBDD', '#FFC851','#FF9393'],
                tooltip: {
                    trigger: 'axis'
                },
                legend: {
                    data: ['总人流', '在镜人流', '入口人流', '出口人流'],
                    textStyle: {
                        show: true,
                        color: '#ffffff',
                        fontSize: 10
                    }
                },
                grid: {
                    left: '3%',
                    right: '4%',
                    top: '15%',
                    bottom: '15%',
                    containLabel: true
                },
                xAxis: {
                    type: 'category',
                    boundaryGap: false,
                    data: [],
                    axisLabel: {
                        show: true,
                        color: '#ffffff',
                        fontSize: 10
                    }
                },
                yAxis: {
                    type: 'value',
                    axisLabel: {
                        show: true,
                        color: '#ffffff',
                        fontSize: 10
                    }
                },
                series: [
                    {
                    name: '在镜人流',
                    type: 'line',
                    stack: 'Total',
                    data: []
                    },
                    {
                    name: '入口人流',
                    type: 'line',
                    stack: 'Total',
                    data: []
                    },
                    {
                    name: '出口人流',
                    type: 'line',
                    stack: 'Total',
                    data: []
                    },
                ]
            }
        }
    },
    created(){
        this.getdata();
    },
    mounted(){
        this.refreshdata=setInterval(() => {
		    this.getdata();
        }, 2000);
	},
    destroyed(){
        
        clearInterval(this.refreshdata);
        this.refreshdata=null;
    },
	methods:{
		getdata(){
            this.axios.get('http://localhost:8000/graph_vis_vue')
            .then(res=>{
					this.content=res.data
				})
            this.option.xAxis.data=this.content.line.time.reverse()
            this.option.series[0].data=this.content.line.vis.reverse()
            this.option.series[1].data=this.content.line.in.reverse()
            this.option.series[2].data=this.content.line.out.reverse()
		},
	}
}
</script>