<template>
    <div style="width:100%;height:100%;">
        <div id="cards">
            <div id="card">{{ content[8] }}</div>
            <div id="card">{{ content[7] }}</div>
            <div id="card">{{ content[6] }}</div>
            <div id="card">{{ content[5] }}</div>
            <div id="card">{{ content[4] }}</div>
        </div>
        <dv-decoration-1 style="width:100%;height:20%;" />
        <div id="nums">
            <div class="row">
                <div class="col-6"><span id="left">场所流量:&ensp;</span><span id="right">{{ content[0] }}</span></div>
                <div class="col-6"><span id="left">可视流量:&ensp;</span><span id="right">{{ content[3] }}</span></div>
            </div>
            <div class="row">
                <div class="col-6"><span id="left">入口流量:&ensp;</span><span id="right">{{ content[1] }}</span></div>
                <div class="col-6"><span id="left">出口流量:&ensp;</span><span id="right">{{ content[2] }}</span></div>
            </div>
        </div>
    </div>
</template>

<script>
export default{
    data(){
		return{
            content:null,
			}
		},
    created(){
        this.getdata();
    },
	mounted(){
        this.refreshdata=setInterval(() => {
		    this.getdata();
        }, 2000);
	},
    destroyed(){
        clearInterval(this.refreshdata);
        this.refreshdata=null;
    },
	methods:{
		getdata(){
			this.axios.get('http://localhost:8000/num_count')
			.then(res=>{
				this.content=res.data
			})
		},
	}
}
</script>

<style scoped>
#cards{
    display: flex;
}
#card {
    flex: 1;
    background-color: rgba(4,49,128,.6);
    border-radius: .25em;
    color: #08e5ff; 
    font-size: 45px;
    font-weight: bold;
    line-height: 70px;
    text-align: center;
    margin: .5em .1em;
}
#nums{
    padding-top: 5%;
}
#left{
    color: white;
    font-size: 1em;
}
#right{
    color: #08e5ff;
    font-weight: bolder;
    font-size: .8em;
}
.row{
    padding: .25em 0;
}
</style>